[role="button"],
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box; }

/* Reset `button` and button-style `input` default styles */
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  background: none;
  border: 0;
  color: inherit;
  /* cursor: default; */
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  -webkit-appearance: button;
  /* for input */
  -webkit-user-select: none;
  /* for button */
  -moz-user-select: none;
  -ms-user-select: none; }

input::-moz-focus-inner,
button::-moz-focus-inner {
  border: 0;
  padding: 0; }

header {
  width: 100%;
  height: 70px;
  line-height: 70px;
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #d7d7d7;
  box-sizing: border-box;
  padding: 0 20px; }

header h1 {
  font-size: 24px;
  display: inline;
  vertical-align: middle;
  flex: 1;
  margin: 0;
  padding: 0; }

header .buttons {
  flex: 1;
  text-align: left; }

header .buttons button {
  display: inline-block;
  border: 1px solid steelblue;
  border-radius: 3px;
  padding: 10px 15px;
  color: steelblue;
  margin-left: 10px; }
  header .buttons button:hover {
    background: steelblue;
    color: white; }

header .selections {
  display: flex;
  text-align: left; }

header .selections label {
  color: #2c3db4;
  padding: 10px 15px; }

header .selections select {
  display: inline-block;
  border: 1px solid steelblue;
  border-radius: 3px;
  padding: 10px 15px;
  color: steelblue;
  margin-left: 10px; }

header .selections button {
  display: inline-block;
  border: 1px solid steelblue;
  border-radius: 3px;
  padding: 10px 15px;
  color: steelblue;
  margin-left: 10px; }
  header .selections button:hover {
    background: steelblue;
    color: white; }

.list-container {
  padding-left: 20px;
  position: absolute;
  bottom: 100px; }

.list ul {
  display: flex; }

.list li {
  display: inline-block;
  vertical-align: bottom;
  position: relative;
  list-style: none;
  width: 70px;
  transition: transform ease-out 1s;
  background-color: #d7d7d7; }
